.page_research {


	// .sect_nav {
	// 	border-bottom: 0.0625rem solid #E5E8EC;
	// 	margin-bottom: rem(56);
	// }

	.articles__items {
		.item {
			flex: 0 0 calc((100% - 2.5rem) / 2);
			max-width: calc((100% - 2.5rem) / 2);

			&__top {
				height: rem(300);
			}
		}
	}

	&__table {
		padding-left: 0;
		padding-right: 0;

		.header_links {
			display: none;
		}

		
	}

}

.wrap_links {
	margin-bottom: 0;

	flex-wrap: wrap;
	flex-direction: row;

	border-top: 0;

	.catalog__left-nav {
		margin-right: rem(20);
	}

	ul {
		flex: 0 0 calc((100% - 4rem) / 3);
		max-width: calc((100% - 4rem) / 3);
		margin-right: rem(32);

		border-bottom: 0;

		&:last-child {
			margin-right: 0;
		}

		li {
			a {
				&::after {
					// content: "\e90d";
					// font-family: 'icons';
					color: var(--c-accent);
					// font-size: rem(22);
				}
			}

			&:last-child {
				border-bottom: 0;
			}
		}
	}
}